<template>
	<div class="recommend">
		<div class="recommend-views">
			<a href="javascript:void(0);">
				<video-player  ref="videoPlayer"
                           :options="playerOptions" >
        		</video-player>
        		<p>1</p>
			</a>
			<a href="javascript:void(0);">
				<video-player  ref="videoPlayer"
                           :options="playerOptions" >
        		</video-player>
        		<p>2</p>
			</a>
			<a href="javascript:void(0);">
				<video-player  ref="videoPlayer"
                           :options="playerOptions" >
        		</video-player>
        		<p>3</p>
			</a>
			<a href="javascript:void(0);">
				<video-player  ref="videoPlayer"
                           :options="playerOptions" >
        		</video-player>
        		<p>4</p>
			</a>
		</div>
	</div>
</template>

<script>
	import { Indicator } from 'mint-ui'
	import { videoPlayer } from 'vue-video-player'

	export default {
		name: 'recommend',
		data() {
			return {
				playerOptions: {
                    	autoplay: false, //true,
        				muted: false, // 
        				loop: false, // 
        				preload: 'auto', // <video>auto,
       					language: 'zh-CN',
        				aspectRatio: '16:9', //  - "16:9""4:3"
        				fluid: true, // trueVideo.js player
        				sources: [{
          					type: "application/x-mpegURL",
          					src: "http://cdn12.mudu.tv/uploads/58661/640_480/d51c6fcf0d2a82f4f80aade7a4eb2ae1.m3u8" //m3u8
        				}],
        				poster: "../../../static/images/poster.jpg", //
        				width: document.documentElement.clientWidth,
            	}
			}
		},
		created() {
			
		},
		mounted(){

		},
		components: {
		  
		},
		methods: {
			
		},
		computed:{
			imgFirst(){
				return '../../../static/images/1.jpg'
			}
		}
	}
</script>
<style lang="scss" >
	@import  "./recommend.scss"

</style>


